var Mask = React.createClass({

    propTypes: {
        showClass: React.PropTypes.string.isRequired,
        closeFunc: React.PropTypes.func.isRequired
    },

    render: function() {
        return (
            <div className={"mask " + this.props.showClass} onClick={this.props.closeFunc}></div>
        );
    }
});

module.exports = Mask;



