var QaPopupBox = require('./QaPopupBox.react');

var QuestionListView = React.createClass({

    render: function(){
        return(
            <li className="questionList">
                <p className="selectWrap noLabel">
                    <select value={this.props.selected} defaultValue="A">
                        <option value="A">顯示近三個月資料</option>
                        <option value="B">2</option>
                        <option value="C">3</option>
                        <option value="D">4</option>
                    </select>
                </p>
                <ul className="questionRecord">
                    <QuestionRecordData />
                    <QuestionRecordData />
                </ul>
            </li>
        );
    }
});

var QuestionRecordData = React.createClass({

    QaRecordLightbox: function(){
        ReactDOM.render(
            <QaPopupBox />, document.getElementById('popupBox'));
    },

    render: function(){
        return(
            <li>
                <p className="questionNumber">
                    <span>問題編號:</span>
                    <span>R13456789</span>
                    <span>2016/01/01</span>
                </p>
                <p className="orderNumber">
                    <span>訂單編號</span>
                    <span>LBO123456789</span>
                </p>
                <p className="productName">
                    <span>產品名稱</span>
                    <span>2015/06/12</span>
                </p>
                <p className="questionContent">
                    <span>問題內容</span>
                    <span>測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容測試內容</span>
                </p>
                <span className="askQuestionBtn">
                    <a className="askAgainBtn" href="">再次發問</a>
                    <a className="questionRecordBtn" href="#" onClick={this.QaRecordLightbox} >問答紀錄</a>
                </span>
            </li>
        )
    }
});


module.exports = QuestionListView;



