var Mask = require('../common/Mask.react');
var QaClientMsg = require('./QaClientMsg.react');
var QaCompanyMsg = require('./QaCompanyMsg.react');

var QaPopupBox = React.createClass({

    componentDidMount: function(){
        ReactDOM.render(
            <Mask showClass="show" closeFunc={() => {}} />, document.getElementById('mask'));
    },

    componentWillUnmount: function() {
        setTimeout(function(){
          ReactDOM.unmountComponentAtNode(document.getElementById('mask'));
      }, 10);
    },

    QaRecordClose: function () {
        setTimeout(function(){
          ReactDOM.unmountComponentAtNode(document.getElementById('popupBox'));
      }, 10);
    },

    render: function(){
        return(
                <div className="popupBox show" >
                    <a href="#" className="close" onClick={this.QaRecordClose} ></a>
                    <div className="popupBoxContent qrlbContent">
                        <div className="qrlbTit">
                            <p>問題編號：R13456789</p>
                        </div>
                        <div className="Dialog">

                            <QaClientMsg />

                            <QaCompanyMsg />

                            <QaCompanyMsg />

                            <QaClientMsg />

                        </div>
                    </div>
                </div>
        );
    }
});


module.exports = QaPopupBox;



