var QuestionFillinView = React.createClass({
    render: function() {
        return (
            <QaFillin qaSteps={qaClass} />
        );
    }
});

var qaClass = [
    {
        type: "payment",
        text: "付款問題",
        questionType: [
            "何時匯款",
            "匯款帳號好像不對",
            "ATM被搬走了",
            "銀行忽然多了好多錢"
        ]
    },
    {
        type: "ship",
        text: "配送問題",
        questionType: [
            "不小心送錯地址",
            "送到天堂了",
            "地獄直達沒有24H嗎"
        ]
    },
    {
        type: "orderQA",
        text: "訂單問題",
        questionType: [
            "訂單是假的",
            "我需要退一千筆訂單",
            "訂單系統怪怪的"
        ]
    }
];

var QaFillin = React.createClass({

    getInitialState: function(){
        return {
            qaName:"",
            qaPhonenumber:"",
            qaEmail:"",
            qaOrdernumber:"",
            qaStorenumber:"",
            qaExplan:"",

            qaClass: [],
            qatype: "",
            qaIndex: null,
            qaSubject: ""
        };
    },

    componentWillMount: function(){
        this.setState({
            qaClass: this.props.qaSteps
        });
    },

    checkQatype: function(e){
        var index = e.target.value;

        if(!!index){
            this.setState({
                qatype: this.state.qaClass[index].type,
                qaIndex: index,
                qaSubject: ""
            });
        }else{
            this.setState({
                qatype: "",
                qaIndex: null,
                qaSubject: ""
            });
        }
    },

    checkQaSubject: function(e){
        this.setState({qaSubject: e.target.value});
    },

    handleQaInput: function(e) {
        var names = e.target.name,
            targetValue = e.target.value;

        switch (names) {
            case 'name':
                this.setState({qaName: targetValue});
                break;
            case 'phonenumber':
                this.setState({qaPhonenumber: targetValue});
                break;
            case 'email':
                this.setState({qaEmail: targetValue});
                break;
            case 'ordernumber':
                this.setState({qaOrdernumber: targetValue});
                break;
            case 'storenumber':
                this.setState({qaStorenumber: targetValue});
                break;
            case 'explan':
                this.setState({qaExplan: targetValue});
                break;
            default:
        }
    },

    render: function(){
        console.log(this.state.qaName+","+this.state.qaPhonenumber+","+this.state.qaEmail+","+this.state.qaOrdernumber+","+this.state.qaStorenumber+","+this.state.qaExplan);

        return(
            <li className="fillinQuestion">
                <p>
                    <label htmlFor="name"><span className="req">*</span>姓名</label>
                    <input type="text" name="name" placeholder="" defaultValue={this.state.qaName} onChange={this.handleQaInput} />
                </p>
                <p>
                    <label htmlFor="phonenumber">連絡電話</label>
                    <input type="text" name="phonenumber" placeholder="" defaultValue={this.state.qaPhonenumber} onChange={this.handleQaInput} />
                </p>
                <p>
                    <label htmlFor="Email"><span className="req">*</span>連絡E-Mail</label>
                    <input type="text" name="email" placeholder="" defaultValue={this.state.qaEmail} onChange={this.handleQaInput} />
                </p>
                <p className="selectWrap">
                    <label htmlFor="Email"><span className="req">*</span>問題類型</label>
                    <select onChange={this.checkQatype}>
                        <option value="">請選擇問題類型</option>
                        {
                            this.state.qaClass.map(function(item, index){
                                return <option value={index} key={index}>{item.text}</option>
                            }, this)
                        }
                    </select>
                </p>
                <p className="selectWrap">
                    <label htmlFor="Email"><span className="req">*</span>問題主旨</label>
                    <select defaultValue={"this.state.qaSubject"} onChange={this.checkQaSubject}>
                        <option value="">請選擇問題主旨</option>
                        {
                            (!!this.state.qaIndex)
                            ?this.state.qaClass[this.state.qaIndex].questionType.map(function(obj, index){
                                return <option value={obj} key={index}>{obj}</option>
                            }, this)
                            : null
                        }
                    </select>
                </p>
                <p>
                    <label htmlFor="Ordernumber">訂單編號</label>
                    <input type="text" name="ordernumber" placeholder="" defaultValue={this.state.Ordernumber} onChange={this.handleQaInput} />
                </p>
                <p>
                    <label htmlFor="Storenumber">賣場編號</label>
                    <input type="text" name="storenumber" placeholder="" defaultValue={this.state.Storenumber} onChange={this.handleQaInput} />
                </p>
                <p>
                    <label htmlFor="explan"><span className="req">*</span>請說明</label>
                    <textarea rows="3" name="explan" defaultValue={this.state.explan} onChange={this.handleQaInput}></textarea>
                </p>
                <a className="send" href="">送出</a>
            </li>
        );
    }
});

module.exports = QuestionFillinView;



