var QuestionFillinView = require('./QuestionFillinView.react');
var QuestionListView = require('./QuestionListView.react');

var QuestionContent = React.createClass({

    render: function(){
        return(
            <div className="askQuestion">
				<div className="title">
					<h3>問答中心</h3>
				</div>
				<div className="askQuestionPage">
					<input type="radio" name="askQuestionTab" id="askQuestionTab01" hidden defaultChecked={{true}} />
					<input type="radio" name="askQuestionTab" id="askQuestionTab02" hidden />
					<div className="askQuestiontabs">
						<label htmlFor="askQuestionTab01">我要發問</label>
						<label htmlFor="askQuestionTab02">問答紀錄</label>
					</div>
					<ul className="askQuestionTabContent">

                        <QuestionFillinView />

                        <QuestionListView />

                    </ul>
				</div>
			</div>
        );
    }
});

module.exports = QuestionContent;



